import React, { useState, useEffect } from 'react'
import { CForm, CButton, CTextarea, CFormGroup, CSelect, CCard, CInputFile, CSpinner, CCardBody, CCardFooter, CSwitch, CCol, CLabel, CRow, CInput, CFormText, CLink, CInputRadio, CAlert }
    from '@coreui/react'
import { CategoriesList as CallCategoriesList } from '../../../src/server/CategoriesManagement'
import { ServicesEdit as EditService } from '../../../src/server/ServicesManagement'
import { Editor } from '@tinymce/tinymce-react';
import { UploadMedia } from '../../../src/server/MediasManagement'


const ServicesEdit = ({ service, setService, setToastMessage, toastMessages, loadServicesList }) => {
    const [categories, setCategories] = useState([])
    const [editingLoader, setEditingLoader] = useState(false)
    const [mediaFile, setMediaFile] = useState()
    const handleEdit = async (ev) => {
        ev.preventDefault()
        setEditingLoader(true)
        const editServiceCallback = async function (mediaId) {
            if (mediaId !== -1) {
                let editObject = {
                    name: service.name,
                    contact: { email: service.contact.email, phone: service.contact.phone },
                    description: service.description,
                    isPublic: service.isPublic ? service.isPublic.toString() : 'true',
                    isIndigenous: service.isIndigenous ? service.isIndigenous.toString() : 'false',
                    category: service.category,
                }
                if (mediaId !== -2)
                    editObject.medias = [mediaId]
                const response = await EditService(service._id, editObject, setToastMessage)
                if (response.ok) {
                    loadServicesList()
                    setToastMessage([
                        { title: 'Congrats!', description: 'The information of service with id: ' + service._id + ' has been successfuly changed', type: 'success' }
                    ])
                }
                else {
                    setToastMessage([
                        { title: 'Error!', description: 'An error occured while processing the request', type: 'danger' }
                    ])
                }
            }
            setEditingLoader(false)
        }
        if (mediaFile)
            await UploadMedia(mediaFile, setToastMessage, editServiceCallback)
        else
            editServiceCallback(-2)
    }
    const loadCategories = async () => {
        const response = await CallCategoriesList()
        const Categories = (await response.json()).data
        setCategories(Categories)
    }
    useEffect(() => {
        loadCategories()
    }, [])
    return (
        <>
            <CCard>
                <CCardBody>
                    <CRow>
                        <CCol sm="12">
                            <h4 id="traffic" className="card-title mb-0">{'ID: ' + service._id}</h4>
                            <div className="small text-muted">{new Date().toDateString()}</div>
                        </CCol>
                    </CRow>
                </CCardBody>
            </CCard>
            <CForm action="" method="service" onSubmit={handleEdit}>
                <CRow>
                    <CCol sm="12">
                        <CCard>
                            <CCardBody>
                                <CFormGroup>
                                    <CLabel htmlFor="name">Service Name</CLabel>
                                    <CInput type="text" id="name" required placeholder="Enter Name..." onInput={(ev) => setService({ ...service, name: ev.target.value })} value={service.name} autoComplete="name" />
                                </CFormGroup>
                                <CFormGroup row>
                                    <CCol sm="4">
                                        <CLabel htmlFor="name">Phone Number:</CLabel>
                                        <CInput type="number" id="phone" placeholder="Enter phone number..." onInput={(ev) => setService({ ...service, contact: { ...service.contact, phone: ev.target.value } })} value={service.contact ? service.contact.phone : ''} autoComplete="phone" />
                                    </CCol>
                                    <CCol sm="4">
                                        <CLabel htmlFor="name">Email:</CLabel>
                                        <CInput type="email" id="email" placeholder="Enter email..." onInput={(ev) => setService({ ...service, contact: { ...service.contact, email: ev.target.value } })} value={service.contact ? service.contact.email : ''} autoComplete="email" />
                                    </CCol>
                                </CFormGroup>
                                <CFormGroup row>
                                    <CLabel col md="2" htmlFor="file-input">Image/Video</CLabel>
                                    <CCol xs="12" md="9" className="ml-n3 mt-1">
                                        <CInputFile id="file-input" name="file-input" onChange={(ev) => setMediaFile(ev.target.files[0])} />
                                    </CCol>
                                </CFormGroup>
                                <CFormGroup>
                                    <CLabel htmlFor="select">Category</CLabel>
                                    <CCol xs="12" md="3">
                                        <CSelect custom name="category" className="ml-n3" id="category" required onChange={(ev) => { setService({ ...service, category: ev.target.value }) }} value={service.category._id}>
                                            {categories.filter(category => category.type === 'general').map(category => {
                                                return <option key={category._id} value={category._id}>{category.name}</option>
                                            })}
                                        </CSelect>
                                    </CCol>
                                </CFormGroup>
                                <CFormGroup row>
                                    <CCol tag="label" sm="4" className="col-form-label mr-n5">
                                        I want this service to be visible:
                                    </CCol>
                                    <CCol sm="1" className="ml-n5">
                                        <CSwitch
                                            onChange={(ev) => setService({ ...service, isPublic: ev.target.checked })}
                                            className="ml-n4 mt-1"
                                            color="info"
                                            checked={service.isPublic ? true : false}
                                        />
                                    </CCol>
                                    <CCol tag="label" sm="3" className="col-form-label mr-n4">
                                        I this service Indigenous:
                                    </CCol>
                                    <CCol sm="1" className="ml-n4">
                                        <CSwitch
                                            onChange={(ev) => setService({ ...service, isIndigenous: ev.target.checked })}
                                            className="ml-n4 mt-1"
                                            color="info"
                                            checked={service.isIndigenous ? true : false}
                                        />
                                    </CCol>
                                </CFormGroup>
                                <CFormGroup>
                                    <CLabel htmlFor="textarea-input">Description</CLabel>
                                    <CTextarea onInput={(ev) => setService({ ...service, description: ev.target.value })} value={service.description}></CTextarea>
                                </CFormGroup>
                            </CCardBody>
                            <CCardFooter className="d-flex">
                                <CButton type="submit" size="md" className="btn btn-info">Edit Service</CButton> <CLink size="sm" className="btn btn-danger ml-2">Cancel</CLink>
                                {editingLoader ? <div className="w-25"><CSpinner className="ml-2" /></div> : null}
                            </CCardFooter>
                        </CCard>
                    </CCol>
                </CRow>
            </CForm>
        </>
    )
}

export default ServicesEdit
