import React, { useEffect, useState } from 'react'
import { CCard, CCardBody, CCol, CRow, CInput, CBadge, CLink, CModal, CModalHeader, CModalTitle, CModalBody, CModalFooter, CButton, CToaster, CToast, CToastHeader, CToastBody }
    from '@coreui/react'
import { ServicesList as CallServicesList, ServicesEdit as CallServicesEdit } from '../../../src/server/ServicesManagement'
import DataTable from 'react-data-table-component';
import CIcon from '@coreui/icons-react'
import { freeSet } from '@coreui/icons'
import ServicesEdit from './ServicesEdit'
import ServicesDetail from './ServicesDetail'

const generateIcon = function (iconName) {
    return (<CIcon content={freeSet[iconName]} />)
}

const ServicesList = () => {
    const [Services, setServices] = useState([])
    const [modalInfo, setModalInfo] = useState({ show: false })
    const [tempServices, setTempServices] = useState([])
    const [searchString, setSearchString] = useState('')
    const [loading, setLoading] = useState(true)
    const [toastMessages, setToastMessage] = useState([])
    const loadServicesList = async () => {
        setLoading(true)
        const response = await CallServicesList()
        const ServicesList = (await response.json()).data
        console.log(ServicesList)
        setServices(ServicesList)
        handleSearch(searchString, ServicesList)
        setLoading(false)
    }
    const handleSearch = (keyword, ServicesList) => {
        keyword = keyword.toLowerCase()
        const ServicesArray = ServicesList ? ServicesList : Services
        const temp = ServicesArray.filter(Service => {
            return Service.name.toLowerCase().includes(keyword) || Service.owner.email.toLowerCase().includes(keyword) || Service.createdAt.toLowerCase().includes(keyword)
        }).sort(function (a, b) { return a.createdAt - b.createdAt }).reverse()
        setTempServices(temp)
        setSearchString(keyword)
    }
    const handleDelete = async (id) => {
        const response = await CallServicesEdit(id, { isDeleted: 'true' }, setToastMessage)
        if (response.ok) {
            setToastMessage([
                { title: 'Congrats!', description: 'The Service with id: ' + id + ' has been successfuly deleted', type: 'success' }
            ])
            await loadServicesList()
        }
        else {
            setToastMessage([
                { title: 'Error!', description: 'An error occured while processing the request', type: 'danger' }
            ])
        }
        console.log(response)
        setModalInfo({ show: false, type: 'delete' })
    }

    const handleIsPublic = async (id) => {
        const response = await CallServicesEdit(id, { isPublic: !modalInfo.isPublic + '' }, setToastMessage)
        if (response.ok) {
            setToastMessage([
                { title: 'Congrats!', description: 'The publicity status of Service with id: ' + id + ' has been successfuly changed', type: 'success' }
            ])
            await loadServicesList()
        }
        else {
            setToastMessage([
                { title: 'Error!', description: 'An error occured while processing the request', type: 'danger' }
            ])
        }
        console.log(response)
        setModalInfo({ show: false })
    }
    useEffect(() => {
        loadServicesList()
    }, [])
    const columns = [
        {
            name: <strong>Name</strong>,
            selector: 'name',
            sortable: true,
            left: true,
        },
        {
            name: <strong>Category</strong>,
            selector: 'category.name',
            sortable: true,
            left: true,
        },
        {
            name: <strong>Phone</strong>,
            selector: 'contact.phone',
            sortable: true,
            left: true,
        },
        {
            name: <strong>Email</strong>,
            selector: 'contact.email',
            sortable: true,
            left: true,
        },
        {
            name: <strong>Is Visible</strong>,
            selector: 'isPublic',
            sortable: true,
            center: true,
            cell: row => <CBadge color={row.isPublic ? 'success' : 'danger'} className="float-right">{row.isPublic ? 'Yes' : 'No'}</CBadge>
        },
        {
            name: '',
            selector: '_id',
            cell: row =>
                <div>
                    <CLink className="card-header-action" onClick={() => setModalInfo({ ...row, show: !modalInfo.show, type: 'detail' })}>
                        {generateIcon('cilInfo')}
                    </CLink>
                    <CLink className="card-header-action" onClick={() => setModalInfo({ ...row, show: !modalInfo.show, type: 'edit' })}>
                        {generateIcon('cilPencil')}
                    </CLink>
                    <CLink className="card-header-action" onClick={() => setModalInfo({ id: row._id, name: row.name, isPublic: row.isPublic, show: !modalInfo.show, type: 'isPublic' })}>
                        {generateIcon('cilCloudUpload')}
                    </CLink>
                    <CLink className="card-header-action" onClick={() => setModalInfo({ id: row._id, name: row.name, show: !modalInfo.show, type: 'delete' })}>
                        {generateIcon('cilTrash')}
                    </CLink>
                </div>,
        },
    ];

    return (
        <>
            <CCard>
                <CCardBody>
                    <CRow>
                        <CCol sm="6">

                        </CCol>
                    </CRow>
                    <CRow>
                        <CCol sm="12">
                            <CInput type="text" id="searchText" placeholder="search by keyword ..." onInput={(ev) => handleSearch(ev.target.value)} />
                            <DataTable
                                title="Services List"
                                columns={columns}
                                data={tempServices}
                                highlightOnHover={true}
                                pagination={true}
                                progressPending={loading}
                            />
                        </CCol>
                    </CRow>
                </CCardBody>
            </CCard>
            <CModal
                show={modalInfo.show}
                onClosed={() => {
                    setModalInfo({ show: false, type: modalInfo.type })
                    setToastMessage([])
                }}
                color={modalInfo.type === 'delete' ? 'danger' : modalInfo.type === 'isPublic' ? 'warning' : 'info'}
                size={(modalInfo.type === 'edit' || modalInfo.type === 'detail') ? 'xl' : ''}
            >
                <CModalHeader closeButton>
                    <CModalTitle>{modalInfo.type === 'delete' ? 'Delete Service account' : modalInfo.type === 'edit' ? 'Edit Service profile' : modalInfo.type === 'detail' ? 'Service detail' : 'Change Publicity status'}</CModalTitle>
                </CModalHeader>
                {modalInfo.show ? <CModalBody>
                    {modalInfo.type === 'delete' ? <div>Are you sure you want to delete <strong>{modalInfo.name}</strong>?</div> : modalInfo.type === 'isPublic' ? <div>Are you sure you want to change publicity status of <strong>{modalInfo.name}</strong>?</div> : null}
                    {modalInfo.type === 'edit' ? <ServicesEdit loadServicesList={loadServicesList} service={modalInfo} setService={setModalInfo} setToastMessage={setToastMessage} toastMessages={toastMessages} /> : modalInfo.type === 'detail' ? <ServicesDetail service={modalInfo} /> : null}
                </CModalBody> : null}
                {modalInfo.type === 'delete' || modalInfo.type === 'isPublic' ? <CModalFooter>
                    <CButton color={modalInfo.type === 'delete' ? 'danger' : 'warning'} onClick={() => modalInfo.type === 'delete' ? handleDelete(modalInfo.id) : handleIsPublic(modalInfo.id)}>Yes</CButton>
                    <CButton
                        color="secondary"
                        onClick={() => setModalInfo({ show: false })}
                    >Cancel</CButton>
                </CModalFooter> : null}
            </CModal>
            {toastMessages.map(toastMessage => {
                console.log(toastMessage)
                return <CToaster
                    position={'bottom-right'}
                    key={'toaster' + Math.floor(Math.random() * 1000000)}
                >
                    {
                        <CToast
                            key={Math.floor(Math.random() * 1000000)}
                            show={true}
                            autohide={3000}
                            fade={true}
                            color={toastMessage.type}
                        >
                            <CToastHeader closeButton={true}>
                                {toastMessage.title}
                            </CToastHeader>
                            <CToastBody>
                                {toastMessage.description}
                            </CToastBody>
                        </CToast>
                    }
                </CToaster>
            })
            }
        </>
    )
}

export default ServicesList
